/**
 *
 */

export default {

  /**
   *    state
   *
   *   this.$store.getters['MODULE_NAMESPACE/getStateVariableName']
   * @param state
   * @returns {null}
   */
  getStateVariableName: (state) => {
    return state.STATE_VARIABLE_NAME;
  },

  /**
   *    state,    payload
   *
   *   this.$store.getters['MODULE_NAMESPACE/getStateVariableName__withPayload']( {id: 1} )
   * @param state
   * @returns
   */
  getStateVariableName__withPayload: (state) => payload => {
    // payload -  
    // payload.id = 1

    return state.STATE_VARIABLE_NAME;
  },
}
