export default {

  //  REST API Laravel Passport
  grant_type: 'password',
  client_id: '',
  client_secret: '',

  //    localStorage
  addToken (token) {
    localStorage.fin_expert__token = token
  },

  //    localStorage
  getToken () {
    let returnValue = ''

    if (typeof localStorage.fin_expert__token !== 'undefined') {
      returnValue = localStorage.fin_expert__token
    }
    return returnValue
  },

  //  token - 
  resetToken () {
    delete localStorage.fin_expert__token
  },
  // 
  logout() {
    this.resetToken();
  },

  //    localStorage  token
  loggedIn() {
    return !!localStorage.fin_expert__token
  }
}
